.search-form-container .nav-tabs.tab-cards {
  display: flex;
}

.searchContainer .search-form-container .nav-tabs .nav-item {
  margin-bottom: 0px;
}

.search-form-container .nav-tabs.tab-cards .nav-link {
  padding: 10px;
  text-align: center;
  border-radius: 0px;
  width: 145px;
  margin-bottom: 0px;
  box-shadow: none;
  border-radius: 4px 4px 0 0 !important;
}
.search-wrapper {
  position: relative;
}
.search-form-container .search-wrapper .form-control {
  border-radius: 0px 0 0 0px;
  padding: 20px 65px 20px 46px;
}

.search-form-container {
  height: 240px;
  background-color: rgba(0, 0, 0, 0.6);
  padding: 16px 18px;
  box-sizing: border-box;
  border-radius: 3px;
}

.searchContainer
  /* .search-form-container */
  /* .nav-tabs.tab-cards */
  /* .nav-item.show */
  /* .nav-link, */
.search-form-container .nav-tabs.tab-cards .nav-link.active {
  background-color: #e4002b;
  color: #fff;
}

.searchContainer .search-form-container .search-wrapper button {
  background: #e4002b;
  width: 133px;
  font-size: 1.125rem;
  height: 60px;
}

.search-form-container .form-control {
  height: 60px;
}

.search-form-container .search-wrapper i {
  position: absolute;
  top: 54%;
  transform: translateY(-50%);
  left: 20px;
  font-size: 18px;
  color: #a2a9bf;
  font-style: normal;
  cursor: normal;
  pointer-events: none;
}

.search-form-container .nav-tabs.tab-cards .nav-item.show .nav-link,
.search-form-container .nav-tabs.tab-cards .nav-link.active:after {
  bottom: -1px;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(255, 255, 255, 0);
  border-bottom-color: #fff;
  border-width: 7px;
  margin-left: -7px;
}

.search-form-container .acr-select2,
.select2-container--default .select2-selection--single {
  background: transparent;
}

.search-form-container .acr-select2,
.search-form-container
  .select2-container--default
  .select2-selection--single:hover {
  background-color: transparent;
}

.search-form-container .acr-custom-select > label {
  color: #fff;
  font-size: 12px;
}

.search-form-container
  .select2-container
  .select2-selection--single
  .select2-selection__rendered {
  /* margin-left: 0px; */
  margin: 0px;
}

.search-form-container
  .select2-container--default
  .select2-selection--single
  .select2-selection__placeholder,
.search-form-container
  .select2-container
  .select2-selection--single
  .select2-selection__rendered {
  padding-left: 0px;
  color: #fff;
}
.banner-title {
  color: white;
  font-size: 65px;
  line-height: 74px;
  font-family: "Poppins", serif;
  font-weight: 700;
}
.subheader {
  padding: 50px 0;
}
.searchContainer {
  position: relative;
  /* top: 60px; */
}

@media only screen and (max-width: 600px) {
  .search-form-container {
    max-height: none;
  }
  .searchContainer .search-form-container .nav-tabs.tab-cards .nav-link {
    width: unset;
    margin: 0px;
  }
  .searchContainer .search-form-container .search-wrapper i {
    top: 52%;
  }
  .searchContainer .search-form-container .sidebar.sidebar-left {
    margin: 0px;
  }
  .searchContainer .search-form-container .nav-tabs.tab-cards .nav-link {
    margin-bottom: 5px;
  }
  .searchContainer .search-form-container .form-group {
    margin-bottom: 0px;
  }
  .banner-title {
    font-size: 38px;
    line-height: 46px;
  }
  .searchContainer {
    padding: 0px;
  }
}
::placeholder {
  color: white;
  opacity: 1; /* Firefox */
}

.select2-container--default .select2-selection--multiple {
  background: #0f0e14 !important;
  border: 1px solid #0f0e14 !important;
}
.searBarInput {
  margin-top: 15px;
  width: 154px;
  background: #0f0b0a;
  border: 1px solid #404040;
  outline: none;
  color: #fff;
  padding: 2px;
  border-radius: 5px;
}

